@import '../../utils/mixins';

.section-title {
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  letter-spacing: 0.15px;

  grid-column: 1 / -1;

  @include on-tablet {
    font-size: 40px;
    margin-bottom: 56px;
  }

  @include on-desktop {
    font-size: 48px;
  }
}
